.tab-item {
    width: 33.3%;
    text-align: center;
    justify-content: center;
    align-items: center;
    display: flex;
    background: #FAFAFA;
    text-decoration: none;
}
.tab-item-icon {
    width: 15px;
    height: 13px;
    vertical-align: middle;
}
.tab-item-name {
    font-size: 12px;
    color: #6F757C;
    margin-left: 5px;
}
.tab-active {
    color: #2491C4;
}
