.kind {
    font-family:PingFangSC-Regular;
    font-weight:400;
    color:rgba(51,51,51,1);
    font-size:0.24rem;
    height: 0.88rem;
    background: #F7FAFE;
    text-align: center;
    overflow: hidden;
    line-height: 0.88rem;
    position: relative;
}
.big {
    font-size:0.28rem;
    border-bottom: 1px rgba(29,29,29,0.1) solid;
}

.select {
    color:rgba(36,145,196,1);
    background: white;
}
.kind_line {
    height: 100%;
    margin-left: 0px;
    width: 4px;
    background:rgba(36,145,196,1);
    display: inline-block;
    position: absolute;
    left: 0;
    top: 0;
}
.kind_line_hidden {
    display: none;
}
